import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';
import linkState from 'linkstate';

import ImageSlider from '../imageSlider';
import StoryComments from './comments';
import StoryAddComment from './commentAdd';

import PublicTeam from '../team/public';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: {},
    viewImageIdx: {},
};

@observer
class StoryPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	likeStory = async (e) => {
        const storyId = parseInt(e.target.closest('button').dataset.id, 10);

		const { storyStore } = this.props.stores;
		const response = await storyStore.likeStory({ id: storyId });
        if (response.status === 200) {
            // this.addLikeToFeed(storyId, response.data);
		}
	}

	toggleAddComment = (e) => {
		const storyId = e.target.closest('button').dataset.id;
		const { showAddComment } = this.state;
        if (showAddComment[storyId]) {
            delete showAddComment[storyId];
        } else {
            showAddComment[storyId] = true;
        }
		this.setState({
			showAddComment,
		}, () => {
			if (this.inputRef[storyId]) {
				this.inputRef[storyId].focus();
			}
		});
	}

    viewImage = (e) => {
		const { id, idx } = e.target.closest('img').dataset;
        const { viewImageIdx } = this.state;
        viewImageIdx[id] = idx;
        this.setState({ viewImageIdx });
    }

    async loadStoriesPublicFeed(props = this.props) {
		const { storyStore } = props.stores;
		await storyStore.loadPublicFeed();
	}

    toggleMenu = (e) => {
        const { id } = e.target.closest('i').dataset;
		const { showMenu = {} } = this.state;
        showMenu[id] = !showMenu[id];
		this.setState({
            showDeleteStory: {},
            showEdit: {},
			showMenu,
		});
	}

    toggleEdit = (e) => {
        const { id, story: storyBody } = e.target.closest('button').dataset;
		const { showEdit = {} } = this.state;
        showEdit[id] = !showEdit[id];
		this.setState({
            showDeleteStory: {},
			showEdit,
            showMenu: {},
            storyBody,
		});
	}

    toggleDeleteStory = (e) => {
        const { id } = e.target.closest('button').dataset;
		const { showDeleteStory = {} } = this.state;
        showDeleteStory[id] = !showDeleteStory[id];
		this.setState({
			showDeleteStory,
            showEdit: {},
            showMenu: {},
		});
	}

    deleteStory = async (e) => {
        const { id } = e.target.closest('button').dataset;
        const { storyStore } = this.props.stores;
        await storyStore.saveField(parseInt(id, 10), 'offline', 1, true);
        storyStore.publicFeedUpdate(parseInt(id, 10), 'offline', 1);
        this.setState({
            storyBody: '',
			showDeleteStory: {},
            showEdit: {},
            showMenu: {},
		});
    }

    saveStory = async (e) => {
        const { id } = e.target.closest('button').dataset;
        const { storyBody: body } = this.state;
        const { storyStore } = this.props.stores;
        // saveField(id, field, value, updateMemory) {
        await storyStore.saveField(parseInt(id, 10), 'body', body, true);
        storyStore.publicFeedUpdate(parseInt(id, 10), 'body', body);
        this.setState({
            storyBody: '',
			showDeleteStory: {},
            showEdit: {},
            showMenu: {},
		});
    }

	componentDidMount() {
        this.loadStoriesPublicFeed();
	}

    render() {
        const { showAddComment, showDate, viewImageIdx, showMenu = {}, showEdit = {}, showDeleteStory = {}, storyBody = '' } = this.state;
        const { userStore, storyStore, appState } = this.props.stores;
        const { user: currentUser, isAdmin } = userStore;
        const { publicFeed } = storyStore;
		const { language = 'no' } = currentUser;
        const { darkmode } = appState;

        return (
            <div class='w-100'>
                {publicFeed && publicFeed.map((story) => {
                    const team = storyStore.findPublicTeam(story.team) || {};
                    const user = storyStore.findPublicUser(story.user) || {};
                    let image;
                    if (team.image && team.image.s3SmallLink) {
                        image = team.image;
                    } else if (team.images && team.images[0] && team.images[0].s3SmallLink) {
                        image = team.images[0];
                    } else if (user.image && user.image.s3SmallLink) {
                        image = user.image;
                    } else if (user.images && user.images[0] && user.images[0].s3SmallLink) {
                        image = user.images[0];
                    }
                    let shortname;
                    let name;
                    let username;
                    if (team && team.name) {
                        shortname = mu.displayNameShort(team);
                        name = mu.displayName(team);
                        username = mu.displayName(user);
                    } else if (user && user.id) {
                        shortname = mu.displayNameShort(user);
                        name = mu.displayName(user);
                    }
                    const { likes, comments } = story;
                    const hasLikedStory = story.likes && story.likes.findIndex(e => e.user === user.id) > -1;
                    const totalFollowers = team && team.followers ? team.followers.length : 0;

                    const isYourTeam = team.members && team.members.indexOf(user.id) > -1;
                    const isFollowing = team && team.followers && team.followers.indexOf(user.id) > -1;
                    const hasRequestedFollow = team.followRequests && team.followRequests.findIndex(e => e.id === user.id) > -1;

                    const images = [];
                    if (story.images) {
                        const imgs = toJS(story.images);
                        images.push(...imgs);
                    }
                    const imageIdx = viewImageIdx[story.id] ? viewImageIdx[story.id] : 0;
                    const currentImage = images && images[imageIdx] ? images[imageIdx] : null;

                    const { currentLocationGeocode = {}, yrWeather = [] } = story;

                    return (<>
                        <div class='row pb-4 bg-light' style={`${story.offline ? 'opacity: 0.4;' : ''}`}>
                            <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}>

                                {currentUser.id === user.id && <>
                                    <span class='float-right font-weight-lighter text-muted'>
                                        <i class='fas fa-ellipsis-h' onClick={this.toggleMenu} data-id={story.id} />
                                    </span>
                                </>}
                                {showMenu[story.id] && <>
                                    <div class='position-absolute bg-white p-2 border rounded rounded-lg shadow-sm' style='top: 30px; right: 10px; width: 60%; z-index: 999999;'>
                                        <button class='btn btn-link btn-block text-left' onClick={this.toggleEdit} data-id={story.id} data-story={story.body}><i class='fas fa-edit' /> Edit</button>
                                        <hr />
                                        <button class='btn btn-link btn-block text-left' onClick={this.toggleDeleteStory} data-id={story.id}><i class='fas fa-trash-alt' /> Delete</button>
                                    </div>
                                </>} 

                                <div class='d-flex'>
                                    <div class={`text-center border rounded-circle imageRounded imageRoundedSmall`} style={image ? `background-image: url("${image.s3SmallLink}");` : ''}>
                                        {!image && <div class='text-muted' style='font-size: 20px; padding-top: 3px;'>{shortname}</div>}
                                    </div>
                                    <div class='flex-grow-1 pl-3 text-overflow' style='line-height: 1.2em;'>
                                        <h5 class='mb-1' style={`line-height: 1.0em; ${story.offline ? 'text-decoration: line-through;' : ''}`}>{name}</h5>
                                        <div class='mb-2' style={`${story.offline ? 'text-decoration: line-through;' : ''}`}>
                                            {story.date && <small class='text-muted' onClick={this.toggleDate}>
                                                {showDate ? util.formatDate(story.date, { locale: 'no-NB', hour12: false, hour: '2-digit', minute: '2-digit' }) : util.formatDistance(story.date, new Date(), { locale: 'no-NB' })}
                                            </small>}
                                            {story.public === 1 ? <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-globe-europe' />
                                                </small>
                                            </> : <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-users' /> ({util.format(totalFollowers)})
                                                </small>
                                            </>}
                                            {username && <>
                                                <small class='ml-2 text-muted'>
                                                    {username}
                                                </small>
                                            </>}
                                            {isAdmin && currentLocationGeocode && currentLocationGeocode.formatted_address && <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-map-marker-alt' /> {currentLocationGeocode.formatted_address}
                                                </small>
                                            </>}

                                            {/* {story.tags && <>
                                                {story.tags.map(tag => <>
                                                    <small class='ml-2 text-muted'>
                                                        {tag}
                                                    </small>
                                                </>)}
                                            </>} */}
                                        </div>
                                    </div>
                                </div>
                                <div class='row mt-2'>
                                    <div class='clearfix col-12' style={`${story.offline ? 'text-decoration: line-through;' : ''}`}>
                                        {showEdit[story.id] ? <>
                                            <div class='d-flex bd-highlight'>
                                                <div class='p-0 w-100 bd-highlight'>
                                                    <label class='sr-only' for='inlineFormInput'><Text id='comments.comment'>Kommentar</Text></label>
                                                    <div class='border rounded rounded-lg position-relative'>
                                                        <Localizer>
                                                            <textarea
                                                                class='form-control-plaintext px-2'
                                                                type='text'
                                                                placeholder={<Text id='comments.write-comment'>Skriv en kommentar...</Text>}
                                                                onInput={linkState(this, 'storyBody')}
                                                                onKeyUp={mu.resizeTextarea}
                                                                onFocus={mu.resizeTextarea}
                                                                style='height: 2.3em;'
                                                                value={storyBody}
                                                            />
                                                        </Localizer>
                                                    </div>
                                                </div>
                                                <div class='p-0 pl-2 flex-shrink-1 bd-highlight'>
                                                    <button type='button' class='btn btn-primary mb-2'
                                                        onClick={this.saveStory}
                                                        data-id={story.id}
                                                    ><i class='fas fa-paper-plane' /></button>
                                                </div>
                                            </div>
                                        </> : <>
                                            <Markdown markdown={mu.replaceImages(story.body, story.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                        </>}
                                        {showDeleteStory[story.id] && <div class='clearfix mt-3 mb-3'>
                                            <button class='btn btn-danger float-left' onClick={this.deleteStory} data-id={story.id}><i class='fas fa-trash-alt' /> Delete</button>
                                            <button class='btn btn-secondary float-right' onClick={this.toggleDeleteStory} data-id={story.id}><i class='fas fa-times-circle' /> Cancel</button>
                                        </div>}
                                    </div>
                                </div>

                                <div class='container-fluid px-0 mt-1'>
                                    {currentImage && <div class='clearfix row'>
                                        <div
                                            class={`col-12 px-0 overflow-hidden`}
                                            style={`max-height: 70vh;`}                                                    
                                        >
                                            <img class={`img-fluid`} style='min-width: 100px;' src={currentImage.s3LargeLink} />
                                        </div>
                                    </div>}
                                    {images && images.length > 1 && <div class='clearfix d-flex flex-wrap row'>
                                        {images.map((img, idx) => {
                                            return (<>
                                                <div
                                                    class={`col px-0 overflow-hidden`}
                                                    style={`max-height: 100px; min-width: 100px;`}                                                    
                                                >
                                                    <img class={`img-fluid border`} style='min-width: 100px;' src={img.s3SmallLink} onClick={this.viewImage} data-id={story.id} data-idx={idx} />
                                                </div>
                                            </>);
                                        })}
                                    </div>}
                                </div>

                                {/* <div class='row'>
                                    <div class='mx-0 px-0 clearfix row'>
                                        {story.images && story.images.map((img, idx) => {
                                            const width_first = 'col-12';
                                            const width_second = 'col';
                                            return (<>
                                                <div class={`px-0 ${idx === 0 ? width_first : width_second} overflow-hidden`} style={`max-height: 60vh;`}>
                                                    <img class={`img-fluid ${idx > 0 ? 'border border-2' : ''}`} style='min-width: 100px;' src={img.s3LargeLink} />                                        
                                                </div>
                                            </>);
                                        })}
                                    </div>
                                </div> */}

                                <div class='mt-2'>
                                    {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                                    </small>}
                                    {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-comment text-primary' /> {comments.length}
                                    </small>}
                                </div>

                                {!isYourTeam && !isFollowing && !hasRequestedFollow && team.public > 0 && <div class='clearfix mb-2'>
                                    <PublicTeam stores={this.props.stores} team={team} buttonOnly={true} />
                                </div>}

                                <div>
                                    <div class='row'>
                                        <div class='col-6 text-center'>
                                            <button type='button' class={`btn btn-sm btn-outline-${hasLikedStory ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedStory ? 'text-primary' : ''}`}
                                                data-id={story.id}
                                                onClick={this.likeStory}
                                                // disabled={hasLikedStory}
                                            >
                                                <i class={`${hasLikedStory ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> <Text id='comments.like'>Lik</Text>
                                            </button>
                                        </div>
                                        <div class='col-6 text-center'>
                                            <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
                                                data-id={story.id}
                                                onClick={this.toggleAddComment}
                                            >
                                                <i class='fas fa-comment' /> <Text id='comments.comment-add'>Kommentar</Text>
                                            </button>
                                        </div>
                                    </div>
                                </div>

                                <div class='mt-2'>
                                    {showAddComment[story.id] && <StoryAddComment
                                        stores={this.props.stores}
                                        story={story}
                                        inputRef={c => this.inputRef[story.id] = c}
                                        inputRefObject={this.inputRef}
                                        col='10'
                                        offset='2'
                                        key={`story-comment-${story.id}`}
                                    />}
                                    <StoryComments
                                        stores={this.props.stores}
                                        story={story}
                                        col='10'
                                        offset='2'
                                        compact={false}
                                    />
                                </div>

                            </div>

                        </div>
                    </>);
                })}
            </div>
        );
    }
}

export default StoryPublicFeed;
