import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import mu from '../../lib/musher-util';

@observer
class YrWeather extends Component {

    toggleWeather = () => {
        const { showWeather } = this.state;
console.log({ showWeather });
        this.setState({
            showWeather: !showWeather,
        });
    }

    render() {
        const { showWeather } = this.state;
        const { data: weather, place } = this.props;
        if (!weather || !place) {
            return '';
        }
        if (showWeather) {
            return (
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-center'>
                    {weather && weather.length > 0 && <div class='container-fluid mb-4'>
                        <div class='row' onClick={this.toggleWeather}>
                            {/* <xmp>{JSON.stringify(weather, null, 4)}</xmp> */}
                            <div class='col-12'>
                                <h5>{place}</h5>
                            </div>
                            {weather && weather.length > 0 && weather.slice(0, 4).map(w => (
                                <div class='col-3 text-center'>
                                    <small class='font-weight-lighter text-muted'>{util.isoTime(w.time)}</small><br />
                                    <img src={`./assets/svg/${util.getNestedValue(w, 'next_1_hours.summary.symbol_code')}.svg`} /><br />
                                    <nobr>
                                        <small>
                                            <i class='fas fa-temperature-low text-muted ml-1' />
                                        </small> {util.getNestedValue(w, 'instant.details.air_temperature')} <span class='text-muted font-weight-lighter'>°C</span>
                                    </nobr><br />
                                    <small class='text-overflow'>
                                        {/* <i class='fas fa-compass text-muted ml-1' /> {mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}<br /> */}
                                        <i class='fas fa-wind text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.wind_speed')}<span class='text-muted font-weight-lighter'>m/s</span> <span class='text-muted font-weight-lighter'>{mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}</span><br />
                                        <i class='fas fa-cloud-rain text-muted ml-1' /> {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount')} <span class='text-muted font-weight-lighter'>mm</span><br />
                                        {/* <i class='fas fa-tint text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.relative_humidity')} <span class='text-muted'>%</span><br /> */}
                                    </small>
                                </div>
                            ))}
                        </div>
                    </div>}
                </div>
            );
        }
        return (
            <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
                {weather && weather.length > 0 && <div class='container-fluid px-0'>
                    <div class='row' onClick={this.toggleWeather}>
                        {weather && weather.length > 0 && weather.slice(0, 1).map(w => (
                            <div class='col-12 text-right'>
                                <nobr>
                                    <small class='font-weight-light text-muted'>{util.isoTime(w.time)}</small>:
                                    <img src={`./assets/svg/${util.getNestedValue(w, 'next_1_hours.summary.symbol_code')}.svg`}
                                        class='ml-2'
                                        style='height: 20px;'
                                    />
                                    {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount') > 0 && <small class='ml-2'>
                                        {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount')}<span class='text-muted font-weight-lighter'>mm</span>
                                    </small>}
                                    <small class='ml-2'>
                                        <i class='fas fa-temperature-low text-muted' /> {util.getNestedValue(w, 'instant.details.air_temperature')}<span class='text-muted font-weight-lighter'>°C</span>
                                    </small> 
                                    <small class='ml-2'>
                                        <i class='fas fa-wind text-muted' /> {util.getNestedValue(w, 'instant.details.wind_speed')}<span class='text-muted font-weight-lighter'>m/s</span> <span class='text-muted font-weight-lighter'>{mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}</span>
                                    </small> 
                                </nobr><br />
                            </div>
                        ))}
                    </div>
                </div>}
            </div>
        );
    }
}

export default YrWeather;
